.favorites-container {
  display: flex;
  position: relative;

  .favorites-btns-container {
    position: absolute;
  }

  .favorite-popup-container {
    margin: auto;
    width: 600px;
  }
}

.favorite-title {
  margin: 5px 5px;
  text-align: center;
  padding: 5px;
  font-size: 30px;
  text-decoration: underline;

  .favorite-none {
    margin: 5px 5px;
    text-align: center;
    padding: 5px;
  }
}